
<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>
          Matches
        </h4>
      </li>
      <li v-for="match in matches"
      v-bind:key="match.id"
      class="collection-item">
        <div class="chip">{{match.name}}</div>
        {{match.frameMax}}
        {{match.match_id}}
        <router-link class="secondary-content" v-bind:to="{name:'view-match', params: {match_id: match.match_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
      <li class="collection-header">
        <h4>
          Player
        </h4>
      </li>
      <li v-for="plyr in players"
      v-bind:key="plyr.id"
      class="collection-item">
        {{plyr.firstName}}
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      matches: [],
      players: []
    }
  },
  created () {
    db.collection('Match').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log(doc)
        const data = {
          'id': doc.id,
          'date': doc.data().matchDate,
          'name': doc.data().name,
          'frameMax': doc.data().frameMax,
          'match_id': doc.data().match_id

        }
        this.matches.push(data)
      })
    })
    db.collection('Player').orderBy('lastName').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log(doc)
        const data = {
          'firstName': doc.data().firstName,
          'lastName': doc.data().lastName,
          'win': doc.data().win,
          'lost': doc.data().lost
        }
        this.players.push(data)
      })
    })
  }
}
</script>



