import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/dashBoard'
import NewMatch from '@/components/newMatch'
import ViewMatch from '@/components/viewMatch'
import EditMatch from '@/components/editMatch'
import Login from '@/components/Login'
import Register from '@/components/Register'
import firebase from 'firebase'

Vue.use(Router)

let router =  new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }  
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: {
        requiresGuest: true
      }  
    },
    {
      path: '/new',
      name: 'new-match',
      component: NewMatch,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/edit/:match_id',
      name: 'edit-match',
      component: EditMatch,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/:match_id',
      name: 'view-match',
      component: ViewMatch,
      meta: {
        requiresAuth: true
      }
    }
  ]
})

router.beforeEach((to,from,next) =>{
  // Check if requiredAuth guard 
  if(to.matched.some(record => record.meta.requiresAuth)){
    // Check if NOT logged in
    if(!firebase.auth().currentUser){
      // Go to login page
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // Proceed to route
      next();
    }
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
    // Check if logged in
    if(firebase.auth().currentUser){
      // Go to login page
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // Proceed to route
      next();
    }
  } else {
    // Proceed to route
    next();
  }
})

export default router;


