<template>
  <div id="view-match">
    <h3>View Match</h3>
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{name}}</h4></li>
      <li class="collection-item">Match ID: {{match_id}}</li>
      <li class="collection-item">Date: {{dateMatch}}</li>
    </ul>

    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteMatch" class="btn red">Delet</button>
    <div class="fixed-action-btn">
      <router-link v-bind:to="{ name: 'edit-match', params:{match_id:match_id}}" class="btn-floating btn-larg red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-match',
  data () {
    return {
      match_id: null,
      dateMatch: null,
      name: null,
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('Match').where('match_id', '==', to.params.match_id).get().then((querySnapshot) => {
      querySnapshot.forEach((doc) => {
         next(vm => {
          vm.match_id = doc.data().match_id
          vm.name = doc.data().name
          vm.dateMatch = doc.data().matchDate
          console.log('doc', doc)
           console.log('vm match id', doc.data().match_id)
           console.log('vm match date', doc.data().dateMatch)
           console.log('vm match 2', doc.data().matchDate)
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      console.log('fetchdata called!')
      db.collection('Match').where('match_id', '==', this.$route.params.match_id).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          this.match_id = doc.data().match_id
          this.name = doc.data().name
          this.dateMatch = doc.data().matchDate
          console.log('match data',doc.data() )
        })
      })
      
       
    },
    deleteMatch(){
      if(confirm('Are you sure ?')) {
        console.log('deleting')
        db.collection('Match').where('match_id', '==', this.$route.params.match_id).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            doc.ref.delete();
            this.$router.push('/')
          })
        })
      }
    }
  }
}
</script>



