<template>
  <div id="new-match">
    <h3>New Employee</h3>
    <div class="row">
    <form @submit.prevent="saveMatch" class="col s12">
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="match_id" required>
          <label>Macth ID</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="name" required>
          <label>Name</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="matchDate" required>
          <label>matchDate</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="maxFrame" required>
          <label>Max frame</label>
        </div>
      </div>
      <button type="submit" class="btn">Submit</button>
      <router-link to="/" class="btn grey">Cancel</router-link>
    </form>
  </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-match',
  data () {
    return {
      match_id: null,
      name: null,
      matchDate: null,
      maxFrame: null
    }
  },
  methods: {
    saveMatch () {
      db.collection('Match').add({
        match_id: this.match_id,
        name: this.name,
        maxFrame: this.maxFrame,
        matchDate: this.matchDate        
      }).then(docRef => {
        console.log('Client added: ',docRef.id)
        this.$router.push('/')
      })
      .catch(error => console.log('Error adding employee:', error))
    }
  }
}
</script>



